import React from "react";
import { Table } from 'reactstrap'
import QRCode from 'qrcode-react';
const Img = require('../img/logo.png')
export default class PrintPayment extends React.Component {
  constructor(props) {
    super(props)
    var date = new Date()
    this.state = {
      dateNow: date.toLocaleString()
    }
  }
  componentWillMount() {
    //console.log(this.props.location.state)
    setTimeout(() => window.print(), 1000)
  }
  render() {
    const { listpay, total, tgl, nama, kelas, jurusan, nisn, key, namadmin } = this.props.location.state
    return (
      <center>
        <div style={{ width: 600 }}>
          <div><b>STRUK PEMBAYARAN ADMINISTRASI SISWA</b></div>
          <div><b>SMK PGRI 2 PONOROGO</b></div>
          <div>Tahun Ajaran 2018 / 2019</div>
          <div>Jl. Soekarno Hatta (0352) 461821 Ponorogo</div>
          <hr />
          <div>
            <div style={{ float: 'left' }} align="left">
              <div>NISN  : {nisn}</div>
              <div>Nama    : {nama}</div>
            </div>
            <div style={{ float: 'right' }} align="right" >
              <div>Kelas: {kelas} - {jurusan}</div>
              <div>Tgl Pembayaran: {tgl}</div>
            </div>
          </div>
          <br />
          <br />
          <hr />
          <Table>
            <thead>
              <tr>
                <th>No</th>
                <th>Nama Pembayaran</th>
                <th>Nominal</th>
              </tr>
            </thead>
            <tbody style={{ fontSize: 12 }}>
              {
                listpay.map((item, idx) => {
                  return (
                    <tr key={idx} >
                      <th>{item.no}</th>
                      <th>{item.nama_pembayaran}</th>
                      <th>{item.nilai}</th>
                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
          <hr />
          <div style={{ float: "left"}} ><b>Dengan Total {total}</b></div>
          <div style={{ float: "right"}} style={{ fontSize: 12, textAlign: 'right' }} >Dicetak {this.state.dateNow}</div>
          <hr />
          <div>
            <div style={{ float: 'left' }} align="left">
              <div style={{ width: 200, height: 200, textAlign: 'center' }} >
                <QRCode value={key} size={128} logo={Img} />
              </div>
            </div>
            <div style={{ float: 'right' }} align="right" >
              <div style={{ width: 250, height: 150, textAlign: 'center' }}>
                PETUGAS ADMINISTRASI
                <br />
                <br />
                <br />
                <br />
                {namadmin}
              </div>
            </div>
          </div>
        </div>
      </center>
    );
  }
}



