import React, { Component } from "react";
import Header from "./Header";
import { Jumbotron, Button } from "reactstrap";
import Information from "./Information";

export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      namadmin: ""
    }
  }
  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    var parsed = JSON.parse(checkLocalStorage)
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.replace('/')
      } else {
        this.setState({
          userLogin: true,
          namadmin: parsed.nama
        })
      }
    }
  }

  componentWillMount() {
    this.checkSession()
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  logoutAlert() {
    if (window.confirm("Logout ?")) {
      this.logOut();
    }
  }
  logOut() {
    localStorage.removeItem("DataLogin");
    sessionStorage.removeItem("DataLogin");
    window.location.replace("/");
  }
  render() {
    return (
      <div>
        <Header />
        <Jumbotron>
          <h1 className="display-4">Selamat Datang Admin {this.state.namadmin} </h1>
          <p className="lead">Gunakan Aplikasi ini dengan bijak</p>
          <Button onClick={() => this.logoutAlert()} color="danger">
            Logout
          </Button>
        </Jumbotron>
        <Information />
      </div>
    );
  }
}



