import React from "react";
import { Table } from 'reactstrap'
export default class ListReports extends React.Component {
  componentWillMount() {
    console.log(this.props.location.state.list);
    setTimeout(() => window.print(), 1000)
  }
  render() {
    const { list, judul, total } = this.props.location.state
    return (
      <div>
        <center><b>LAPORAN PEMBAYARAN ADMINISTRASI SISWA</b></center>
        <center><b>SMK PGRI 2 PONOROGO</b></center>
        <center><b>{judul}</b></center>
        <hr />
        <Table>
          <thead>
            <tr>
              <th>Nama</th>
              <th>Kelas</th>
              <th>Nama Pembayaran</th>
              <th>Nominal</th>
              <th>Tgl Pembayaran</th>
              <th>Petugas</th>
            </tr>
          </thead>
          <tbody style={{ fontSize: 12 }}>
            {list.map((item, idx) => {
              return (
                <tr key={idx}>
                  <th scope="row">{item.nama}</th>
                  <td>{item.kelas} - {item.jurusan}</td>
                  <td>{item.nama_pembayaran}</td>
                  <td>{item.nilai}</td>
                  <td>{item.tgl_pembayaran}</td>
                  <td>{item.admin}</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
        <hr />
        <center><b>Dengan Total {total}</b></center>
      </div>
    );
  }
}



