import { CSVLink } from 'react-csv'
import Header from "./Header";
import ReactTable from "react-table"
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { Row, Input, Col, Button, } from 'reactstrap'
const GLOBAL = require('../api')
export default class Report extends Component {
  constructor(props) {
    super(props)
    var date = new Date()
    this.state = {
      list: [],
      total: '',
      judul: '',
      costumDate: false,
      start: '',
      end: '',
      reSult: false,
      print: false,
      tgl: date.getDate(),
      bln: date.getMonth(),
      thn: date.getFullYear(),
      fulldate: date.toLocaleDateString()
    }
  }

  getDataHarian() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/harian')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Tanggal ' + this.state.fulldate, reSult: true })
        }
      })
  }
  getDataBulanan() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/bulanan')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Bulan ' + this.state.bln + "/" + this.state.thn, reSult: true })
        }
      })
  }
  getDataTahunan() {
    this.setState({
      costumDate: false
    })
    fetch(GLOBAL.LINK + '/laporan/tahunan')
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Tahun ' + this.state.thn, reSult: true })
        }
      })
  }
  getDataCustom() {
    this.setState({
      costumDate: true
    })
  }
  getResultCustom() {
    fetch(GLOBAL.LINK + '/laporan/custom/' + this.state.start + '/' + this.state.end)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ list: [], reSult: false, res: '' })
          alert('Tidak ada data')
        } else {
          this.setState({ list: res.records, total: res.total, judul: 'Tanggal ' + this.state.start + ' / ' + this.state.end, reSult: true })
        }
      })
  }

  checkSession() {
    var checkLocalStorage = localStorage.getItem('DataLogin')
    var checkSessionStorage = sessionStorage.getItem('DataLogin')
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert('Sesi telah berakhir, silahkan login kembali')
        sessionStorage.clear()
        localStorage.clear()
        window.location.replace('/')
      }
    } else {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.replace('/')
    }
    if (checkSessionStorage === null) {
      alert('Sesi telah berakhir, silahkan login kembali')
      sessionStorage.clear()
      localStorage.clear()
      window.location.replace('/')
    }
  }

  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000)
  }
  componentWillUnmount() {
    clearInterval(this.timer)
  }
  render() {
    // data for csv
    const data = this.state.list

    //title header csv
    const headers = [
      { label: 'Nama', key: 'nama' },
      { label: 'Kelas', key: 'kelas' },
      { label: 'Jurusan', key: 'jurusan' },
      { label: 'Nama Pembayaran', key: 'nama_pembayaran' },
      { label: 'Nilai', key: 'nilai' },
      { label: 'Tgl Pembayaran', key: 'tgl_pembayaran' },
      { label: 'Petugas', key: 'admin' },
    ];

    //title table list
    const colums = [
      {
        Header: "Nama",
        accessor: "nama",
        width: 300
      },
      {
        Header: "Kelas",
        id: "kelas",
        accessor: d => d.kelas + " - " + d.jurusan,
        style: {
          textAlign: 'center'
        }
      },
      {
        Header: "Nama Pembayaran",
        id: "payname",
        accessor: d => d.nama_pembayaran
      },
      {
        Header: "Nilai",
        id: "nilai",
        accessor: d => d.nilai
      },
      {
        Header: "Tgl",
        id: "tgl",
        accessor: d => d.tgl_pembayaran,
        style: {
          textAlign: 'center'
        }
      },
      {
        Header: "Petugas",
        id: "petugas",
        accessor: d => d.admin
      },
    ]
    return (
      <div>
        <Header />
        <Col>
          <Button color="primary" onClick={() => this.getDataHarian()} > Harian </Button>{" "}
          <Button color="primary" onClick={() => this.getDataBulanan()} > Bulanan </Button>{" "}
          <Button color="primary" onClick={() => this.getDataTahunan()}> Tahunan </Button>{" "}
          {
            this.state.costumDate ?
              <Button color="primary" onClick={() => this.getResultCustom()}> Proses </Button> :
              <Button color="primary" onClick={() => this.getDataCustom()}> Custom </Button>
          }
          <hr />
          {
            this.state.costumDate ?
              <Row>
                <Col>
                  <Input onChange={event => this.setState({ start: event.target.value })} type="date" />
                </Col>
                <Col>
                  <Input onChange={event => this.setState({ end: event.target.value })} type="date" />
                </Col>
              </Row>
              : null
          }
          {
            this.state.reSult ?
              <div>
                <hr />
                <center><b>LAPORAN PEMBAYARAN ADMINISTRASI SISWA</b></center>
                <center><b>SMK PGRI 2 PONOROGO</b></center>
                <center><b>{this.state.judul}</b></center>
                <br />
                <br />
                <ReactTable
                  showPaginationTop={true}
                  showPaginationBottom={false}
                  data={this.state.list}
                  columns={colums}
                  defaultPageSize={10}
                  className="-striped -highlight"
                />
                <hr />
                <center><b> Dengan total : {this.state.total}  </b></center>
                <hr />
                <CSVLink data={data} filename={this.state.judul + ".csv"} headers={headers} >Download Report</CSVLink>
                {" "}
                <Link to={{
                  pathname: "/print",
                  state: {
                    list: this.state.list,
                    judul: this.state.judul,
                    total: this.state.total
                  }
                }}> Print </Link>
              </div> : null
          }
        </Col>
      </div>
    );
  }
}



