import React, { Component } from "react";
import { Container } from "reactstrap";
import { Route, BrowserRouter } from "react-router-dom";

import Home from "./pages/Home";
import Siswa from "./pages/Siswa";
import Settings from "./pages/Settings";
import Account from "./pages/Account";
import Report from "./pages/Laporan";
import ListReports from "./pages/ListReport";
import PrintPayment from "./pages/PrintPayment";
import Login from "./pages/Login";

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nama: "",
      login: false
    };
  }
  checkSession() {
    var checkLocalStorage = localStorage.getItem("DataLogin");
    var checkSessionStorage = sessionStorage.getItem("DataLogin");
    var parsed = JSON.parse(checkLocalStorage);
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert("Sesi telah berakhir, silahkan login kembali");
        sessionStorage.clear();
        localStorage.clear();
        window.location.reload();
      } else {
        this.setState({
          nama: parsed.nama,
          login: true
        });
      }
    }
  }

  componentWillMount() {
    this.checkSession();
  }
  render() {
    return (
      <BrowserRouter>
        <div style={{ padding: 20 }}>
          <Container>
            {this.state.login ? (
              <div>
                <Route path="/dashboard" component={Home} />
                <Route path="/siswa" component={Siswa} />
                <Route path="/pengaturan" component={Settings} />
                <Route path="/akun" component={Account} />
                <Route path="/laporan" component={Report} />
                <Route path="/print" component={ListReports} />
                <Route path="/printstruk" component={PrintPayment} />
              </div>
            ) : (
                <div>
                  <Route exact path="/" component={Login} />
                </div>
              )}
          </Container>
        </div>
      </BrowserRouter>
    );
  }
}



