import React from "react";
import { Button, Form, FormGroup, Input, Row, Col, Container } from "reactstrap";
const GLOBAL = require("../api");

export default class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
    };
  }
  componentWillMount() {
    this.props.history.push("/");
  }
  pressLogin() {
    const { username, password } = this.state;
    if (username === "") alert("Username kosong");
    else if (password === "") alert("Password kosong");
    else {
      const data = new FormData();
      data.append("username", username);
      data.append("password", password);
      fetch(GLOBAL.LINK + "/login", {
        method: "POST",
        header: {
          Accept: "application/json",
          "Content-type": "application/json"
        },
        body: data
      })
        .then(respone => respone.json())
        .then(res => {
          if (res.status === "302") {
            console.log(res);
            localStorage.setItem("DataLogin", JSON.stringify(res.data));
            sessionStorage.setItem("DataLogin", JSON.stringify(res.data));
            window.location.replace("/dashboard");
          } else {
            alert("Login gagal");
          }
        });
    }
  }

  render() {
    return (
      <Container>
        <Row>
          <Col>
            <b>Login admin</b>
            <hr />
            <Form>
              <FormGroup>
                <Input
                  onChange={event =>
                    this.setState({ username: event.target.value })
                  }
                  value={this.state.username}
                  type="text"
                  placeholder="username"
                />
              </FormGroup>
              <FormGroup>
                <Input
                  onChange={event =>
                    this.setState({ password: event.target.value })
                  }
                  value={this.state.password}
                  type="password"
                  placeholder="password"
                />
              </FormGroup>
              <hr />
              <Button onClick={() => this.pressLogin()} color="primary">
                Submit
              </Button>{" "}
            </Form>
          </Col>
        </Row>
      </Container>
    );
  }
}



