import React, { Component } from "react";
import ReactTable from "react-table";
import Header from "./Header";
import { Link } from "react-router-dom";
import { FormGroup, Label, Input, Row, Col, Table, Button } from "reactstrap";
const GLOBAL = require("../api");
export default class Siswa extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //option
      jurusan: [], kelas: "pilih", vjurusan: "pilih",

      //siswa
      siswa: [],

      //admin
      namadmin: "",

      //student selected
      nisn: "", idortu: "", nama: "", vkelas: "", vjur: "", idsiswa: "",

      //change password
      pass1: "", pass2: "",

      //component view
      tableView: false, selected: false, payment: false, done: false, sendMessage: false, passwordChange: false,

      //payment
      namapem: [], id_pem: "pilih", kategori: "pilih", nilai: "", daftar: [],

      //payemnt update
      updatePayment: false,
      idbyr: "",

      //inbox create
      judul: "", msg: "",

      //for struk
      listpay: [], sum: "", paydate: "", key: ""
    };
    this.changeKelas = this.changeKelas.bind(this);
    this.changeJurusan = this.changeJurusan.bind(this);
    this.changePembayaran = this.changePembayaran.bind(this);
    this.changeKategoriPembayaran = this.changeKategoriPembayaran.bind(this);
    this.changeNilai = this.changeNilai.bind(this);
  }

  //get data
  getJurusan() {
    fetch(GLOBAL.LINK + "/kelas")
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({
            jurusan: []
          });
        } else {
          this.setState({
            jurusan: res.records
          });
        }
      });
  }
  getNamaPembayaran() {
    this.setState({ payment: true, passwordChange: false, sendMessage: false, pass1: "", pass2: "" });
    fetch(GLOBAL.LINK + "/nama_pembayaran/" + this.state.nisn)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ namapem: [] });
        } else {
          this.setState({ namapem: res.records });
          this.getDaftarPembayaran();
          this.getCreateStruk();
        }
      });
  }
  getNamaPembayaranForEdit() {
    fetch(GLOBAL.LINK + "/nama/pembayaran")
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ namapem: [] });
        } else {
          this.setState({ namapem: res.records });
          this.getDaftarPembayaran();
        }
      });
  }
  getDaftarPembayaran() {
    fetch(GLOBAL.LINK + "/pembayaran/" + this.state.nisn)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ daftar: [] });
        } else {
          this.setState({ daftar: res.records });
          this.getCreateStruk();
        }
      });
  }
  getCreateMessage() {
    fetch(GLOBAL.LINK + "/inbox/create/" + this.state.nisn)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ judul: "", msg: "", total: "", tgl: "" });
        } else {
          this.setState({
            judul: res.record.judul,
            msg: this.state.nama + " " + res.record.pesan + "\n\nDengan total \n" + res.record.total + "\n\nPetugas \n" + this.state.namadmin
          });
        }
      });
  }

  getCreateStruk() {
    fetch(GLOBAL.LINK + "/inbox/create/struk/" + this.state.nisn)
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({ sum: "", listpay: "" });
        } else {
          this.setState({
            sum: res.record.total,
            listpay: res.record.pembayaran,
            paydate: res.record.tgl,
            key: res.record.key
          });
        }
      });
  }

  //search data
  searchData() {
    fetch(
      GLOBAL.LINK + "/siswa/" + this.state.kelas + "/" + this.state.vjurusan
    )
      .then(response => response.json())
      .then(res => {
        if (res.status === 404) {
          this.setState({
            siswa: [],
            tableView: false,
            payment: false,
            selected: false
          });
          alert("Data tidak ada");
        } else {
          this.setState({
            siswa: res.records,
            tableView: true,
            payment: false
          });
        }
      });
  }

  //mount component
  componentDidMount() {
    this.getJurusan();
  }

  //change function
  changeKelas(event) {
    this.setState({ kelas: event.target.value });
  }
  changeJurusan(event) {
    this.setState({ vjurusan: event.target.value });
  }
  changePembayaran(event) {
    this.setState({ id_pem: event.target.value, fadeIn: false });
  }
  changeKategoriPembayaran(event) {
    this.setState({ kategori: event.target.value, fadeIn: false });
  }
  changeNilai(event) {
    this.setState({ nilai: event.target.value });
  }

  //change password
  changePassword() {
    const { idortu, pass1, pass2 } = this.state;
    if (pass1 !== pass2) alert("Password tidak sama");
    else {
      const data = new FormData();
      data.append("id", idortu);
      data.append("passnew", pass1);
      fetch(GLOBAL.LINK + "/reset/password", {
        method: "POST",
        header: {
          Accept: "application/json",
          "Content-type": "application/json"
        },
        body: data
      })
        .then(respone => respone.json())
        .then(res => {
          if (res.status === 200) {
            alert("Password diperbarui");
            this.setState({
              passwordChange: false,
              payment: false,
              sendMessage: false,
              pass1: "",
              pass2: ""
            });
          } else {
            alert("Password gagal diperbarui");
          }
        });
    }
  }

  //cancel update
  cancelEditPayment() {
    this.setState({
      id_pem: "pilih",
      kategori: "pilih",
      nilai: "",
      updatePayment: false
    });
    this.getNamaPembayaran();
  }

  //selected funcion
  selectedSiswa(nisn, id, nama, kelas, jurusan, idsiswa) {
    this.setState({
      nisn: nisn,
      idortu: id,
      nama: nama,
      vkelas: kelas,
      vjur: jurusan,
      idsiswa: idsiswa,
      selected: true,
      payment: false
    });
  }
  selectedPayment(id_pem, kategori, nilai, idbyr) {
    this.setState({
      id_pem: id_pem,
      kategori: kategori,
      nilai: nilai,
      idbyr: idbyr,
      updatePayment: true
    });
    this.getNamaPembayaranForEdit();
  }

  //done button
  donePayment() {
    this.setState({
      done: true
    });
    this.getCreateMessage();
  }

  //fcm
  FirebaseNotification(to, title, msg) {
    fetch("https://fcm.googleapis.com/fcm/send", {
      method: "POST",
      headers: {
        authorization: "key=AIzaSyB0UGwE1GX2QUKzWoZpOang9FYdLOz1II8",
        "content-type": "application/json"
      },
      body: JSON.stringify({
        to: "/topics/" + to,
        data: {
          custom_notification: {
            title: title,
            body: msg,
            color: "#c62828",
            priority: "high",
            icon: "ic_notif",
            sound: "default",
            show_in_foreground: true,
            wake_screen: true
          }
        }
      })
    });
  }



  //send notif
  sendNotificationMessage() {
    const { nisn, judul, msg, namadmin } = this.state;
    var data = new FormData();
    data.append("nisn", nisn);
    data.append("judul", judul);
    data.append("isipesan", msg);
    data.append("admin", namadmin);
    if (this.state.sendMessage) {
      data.append("type", "1");
    } else {
      data.append("type", "0");
    }
    fetch(GLOBAL.LINK + "/inbox/send", {
      method: "POST",
      header: {
        Accept: "application/json",
        "Content-type": "application/json"
      },
      body: data
    })
      .then(respone => respone.json())
      .then(res => {
        if (res.status === 201) {
          this.setState({
            done: false,
            sendMessage: false,
            judul: "",
            msg: ""
          });
          this.getDaftarPembayaran();
          this.getNamaPembayaran();
          this.FirebaseNotification(this.state.idsiswa, judul, msg);
          alert("Notifikasi dikirim");
        } else {
          this.setState({
            done: false
          });
          alert("Notifikasi gagal dikirim");
        }
      });
  }

  //add payment
  paymentAdd() {
    const { id_pem, kategori, nisn, nilai, namadmin } = this.state;
    if (id_pem === "pilih") {
      alert("Nama Pembayaran belum dipilih");
    } else if (kategori === "pilih") {
      alert("Kategori Pembayaran belum dipilih");
    } else if (nilai === "") {
      alert("Nilai kosong");
    } else {
      var data = new FormData();
      data.append("nisn", nisn);
      data.append("id_pembayaran", id_pem);
      data.append("kategori", kategori);
      data.append("nilai", nilai);
      data.append("admin", namadmin);
      fetch(GLOBAL.LINK + "/pembayaran/add", {
        method: "POST",
        header: {
          Accept: "application/json",
          "Content-type": "application/json"
        },
        body: data
      })
        .then(response => response.json())
        .then(res => {
          if (res.status === 201) {
            this.setState({
              kategori: "pilih",
              id_pem: "pilih",
              nilai: "",
              updatePayment: false
            });
            this.getNamaPembayaran();
            this.getCreateStruk();
          } else {
            this.setState({ kategori: "pilih", id_pem: "pilih", nilai: "" });
          }
        });
    }
  }

  //update payment
  paymentUpdate() {
    const { id_pem, kategori, nisn, nilai, namadmin } = this.state;
    if (id_pem === "pilih") {
      alert("Nama Pembayaran belum dipilih");
    } else if (kategori === "pilih") {
      alert("Kategori Pembayaran belum dipilih");
    } else if (nilai === "") {
      alert("Nilai kosong");
    } else {
      var data = new FormData();
      data.append("nisn", nisn);
      data.append("id_pembayaran", id_pem);
      data.append("kategori", kategori);
      data.append("nilai", nilai);
      data.append("admin", namadmin);
      fetch(GLOBAL.LINK + "/pembayaran/update/" + this.state.idbyr, {
        method: "POST",
        header: {
          Accept: "application/json",
          "Content-type": "application/json"
        },
        body: data
      })
        .then(response => response.json())
        .then(res => {
          if (res.status === 200) {
            this.setState({
              kategori: "pilih",
              id_pem: "pilih",
              nilai: "",
              updatePayment: false
            });
            alert("Berhasil di update");
            this.getNamaPembayaran();
            this.getDaftarPembayaran();
            this.getCreateStruk();
          } else {
            alert("Gagal di update");
            this.setState({ kategori: "pilih", id_pem: "pilih", nilai: "" });
          }
        });
    }
  }

  //delete payment
  deleteAlert(id) {
    if (window.confirm("Hapus pembayaran")) {
      this.deletePayment(id);
    }
  }

  deletePayment(id) {
    fetch(GLOBAL.LINK + "/pembayaran/delete/" + id, {
      method: "DELETE"
    })
      .then(respone => respone.json())
      .then(res => {
        if (res.status === 200) {
          alert("Berhasil dihapus");
          this.getNamaPembayaran();
          this.getDaftarPembayaran();
          this.getCreateMessage();
          this.getCreateStruk();
        } else {
          alert("Gagal dihapus");
        }
      });
  }

  checkSession() {
    var checkLocalStorage = localStorage.getItem("DataLogin");
    var checkSessionStorage = sessionStorage.getItem("DataLogin");
    var parsed = JSON.parse(checkLocalStorage);
    if (checkLocalStorage !== null) {
      if (checkLocalStorage !== checkSessionStorage) {
        alert("Sesi telah berakhir, silahkan login kembali");
        sessionStorage.clear();
        localStorage.clear();
        window.location.replace("/");
      } else {
        this.setState({
          userLogin: true,
          namadmin: parsed.nama
        });
      }
    } else {
      alert("Sesi telah berakhir, silahkan login kembali");
      sessionStorage.clear();
      localStorage.clear();
      window.location.replace("/");
    }
    if (checkSessionStorage === null) {
      alert("Sesi telah berakhir, silahkan login kembali");
      sessionStorage.clear();
      localStorage.clear();
      window.location.replace("/");
    }
  }

  componentWillMount() {
    this.timer = setInterval(() => this.checkSession(), 1000);
  }
  componentWillUnmount() {
    clearInterval(this.timer);
  }
  render() {
    return (
      <div>
        <Header />
        <Row>
          <Col sm="6">
            <Col xs="auto">
              <FormGroup>
                <Input
                  value={this.state.kelas}
                  onChange={this.changeKelas}
                  type="select"
                  name="select"
                  id="exampleSelect"
                >
                  <option value="pilih">Pilih Kelas</option>
                  <option value="X">X</option>
                  <option value="XI">XI</option>
                  <option value="XII">XII</option>
                </Input>
              </FormGroup>
              <FormGroup>
                <Input
                  value={this.state.vjurusan}
                  onChange={this.changeJurusan}
                  type="select"
                  name="select"
                  id="exampleSelect"
                >
                  <option value="pilih">Pilih Jurusan</option>

                  {this.state.jurusan.map((item, key) => {
                    return (
                      <option key={key} value={item.jurusan}>
                        {item.jurusan}
                      </option>
                    );
                  })}
                </Input>
              </FormGroup>
              <Button color="primary" onClick={() => this.searchData()}>
                Proses
              </Button>
            </Col>
            <hr />
            <Col xs="auto">
              {this.state.tableView ? (
                <div style={{ height: 300, overflow: "scroll" }}>
                  <Table>
                    <thead>
                      <tr>
                        <th>NISN</th>
                        <th>Nama</th>
                        <th>opsi</th>
                      </tr>
                    </thead>
                    <tbody style={{ fontSize: 12 }}>
                      {this.state.siswa.map((item, idx) => {
                        return (
                          <tr key={idx}>
                            <td>{item.nisn}</td>
                            <th scope="row">{item.nama}</th>
                            <td>
                              <Button
                                color="info"
                                onClick={() =>
                                  this.selectedSiswa(
                                    item.nisn,
                                    item.idortu,
                                    item.nama,
                                    item.kelas,
                                    item.jurusan,
                                    item.id
                                  )
                                }
                              >
                                Pilih
                              </Button>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </Table>
                </div>
              ) : null}
            </Col>
          </Col>
          <Col sm="6">
            {this.state.selected ? (
              <div>
                <Row>
                  <Col xs="6">
                    <FormGroup>
                      <Label for="exampleEmail">NISN</Label>
                      <Input
                        disabled="true"
                        type="text"
                        value={this.state.nisn}
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Nama</Label>
                      <Input
                        disabled="true"
                        type="text"
                        value={this.state.nama}
                      />
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label for="exampleEmail">ID Ortu</Label>
                      <Input
                        disabled="true"
                        type="text"
                        value={this.state.idortu}
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Kelas</Label>
                      <Input
                        disabled="true"
                        type="text"
                        value={this.state.vkelas + " - " + this.state.vjur}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <Button
                      color="primary"
                      onClick={() => this.getNamaPembayaran()}
                    >
                      Pembayaran
                    </Button>{" "}
                    <Button
                      color="warning"
                      onClick={() =>
                        this.setState({
                          sendMessage: true,
                          payment: false,
                          passwordChange: false,
                          done: false,
                          msg: "",
                          judul: "",
                          pass1: "",
                          pass2: ""
                        })
                      }
                    >
                      Kirim Pesan
                    </Button>{" "}
                    <Button
                      color="success"
                      onClick={() =>
                        this.setState({
                          passwordChange: true,
                          payment: false,
                          sendMessage: false,
                          done: false
                        })
                      }
                    >
                      Ganti Password
                    </Button>{" "}
                  </Col>
                </Row>
                <hr />
              </div>
            ) : null}
            <Row>
              <Col sm="12">
                <Col xs="auto">
                  {/* Component for Payment */}
                  {this.state.done ? (
                    <div>
                      <FormGroup>
                        <Input
                          type="text"
                          disabled="true"
                          value={this.state.judul}
                          placeholder="Judul pesan"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Input
                          type="textarea"
                          disabled="true"
                          name="text"
                          id="exampleText"
                          value={this.state.msg}
                          placeholder="Isi pesan"
                        />
                      </FormGroup>
                      <Button
                        color="primary"
                        onClick={() => this.sendNotificationMessage()}
                      >
                        Kirim
                      </Button>{" "}
                      <Button
                        color="secondary"
                        onClick={() =>
                          this.setState({ done: false, msg: "", judul: "" })
                        }
                      >
                        Batal
                      </Button> {" "}
                      <Link
                        to={{
                          pathname: "/printstruk",
                          state: {
                            listpay: this.state.listpay,
                            total: this.state.sum,
                            tgl: this.state.paydate,
                            nisn: this.state.nisn,
                            nama: this.state.nama,
                            kelas: this.state.vkelas,
                            jurusan: this.state.vjur,
                            key: this.state.key,
                            namadmin: this.state.namadmin
                          }
                        }}
                      >
                        Cetak Struk
                      </Link>
                    </div>
                  ) : (
                      <div>
                        {this.state.payment ? (
                          <div>
                            <FormGroup>
                              <Input
                                value={this.state.id_pem}
                                onChange={this.changePembayaran}
                                type="select"
                                name="select"
                                id="exampleSelect"
                              >
                                <option value="pilih">
                                  Pilih Nama Pembayaran
                              </option>
                                {this.state.namapem.map((item, idx) => {
                                  return (
                                    <option key={idx} value={item.id}>
                                      {item.nama_pembayaran}
                                    </option>
                                  );
                                })}
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Input
                                value={this.state.kategori}
                                onChange={this.changeKategoriPembayaran}
                                type="select"
                                name="select"
                                id="exampleSelect"
                              >
                                <option value="pilih">KATEGORI</option>
                                <option value="spp">SPP</option>
                                <option value="dispp">DI SPP</option>
                                <option value="koperasi">KOPERASI</option>
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Input
                                type="text"
                                pattern="[0-9]*"
                                onChange={this.changeNilai}
                                required="ture"
                                value={this.state.nilai}
                                placeholder="Nominal ( tanpa . )"
                              />
                            </FormGroup>
                            {this.state.updatePayment ? (
                              <div>
                                <Button
                                  color="primary"
                                  onClick={() => this.paymentUpdate()}
                                >
                                  Update
                              </Button>{" "}
                                <Button
                                  color="secondary"
                                  onClick={() => this.cancelEditPayment()}
                                >
                                  Batal
                              </Button>
                              </div>
                            ) : (
                                <div>
                                  <Button
                                    color="primary"
                                    onClick={() => this.paymentAdd()}
                                  >
                                    Simpan
                              </Button>{" "}
                                  <Button
                                    color="success"
                                    onClick={() => this.donePayment()}
                                  >
                                    Selesai
                              </Button>{" "}
                                </div>
                              )}
                          </div>
                        ) : null}
                      </div>
                    )}

                  {/* Component for Send Message */}
                  {this.state.sendMessage ? (
                    <div>
                      <FormGroup>
                        <Input
                          type="text"
                          onChange={event =>
                            this.setState({ judul: event.target.value })
                          }
                          value={this.state.judul}
                          placeholder="Judul pesan"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Input
                          type="textarea"
                          onChange={event =>
                            this.setState({ msg: event.target.value })
                          }
                          name="text"
                          id="exampleText"
                          value={this.state.msg}
                          placeholder="Isi pesan"
                        />
                      </FormGroup>
                      <Button
                        color="primary"
                        onClick={() => this.sendNotificationMessage()}
                      >
                        Kirim
                      </Button>
                    </div>
                  ) : null}

                  {/* Component for Change Password */}
                  {this.state.passwordChange ? (
                    <div>
                      <FormGroup>
                        <Input
                          type="password"
                          onChange={event =>
                            this.setState({ pass1: event.target.value })
                          }
                          value={this.state.pass1}
                          placeholder="Password baru"
                        />
                      </FormGroup>
                      <FormGroup>
                        <Input
                          type="password"
                          onChange={event =>
                            this.setState({ pass2: event.target.value })
                          }
                          value={this.state.pass2}
                          placeholder="Konfirmasi password baru"
                        />
                      </FormGroup>
                      <Button
                        color="primary"
                        onClick={() => this.changePassword()}
                      >
                        Perbarui
                      </Button>
                    </div>
                  ) : null}
                </Col>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row>
          {this.state.payment ? (
            <Col>
              <hr />
              <b>Yang telah dibayar</b>
              <br />
              <br />
              <ReactTable
                showPaginationTop={true}
                showPaginationBottom={false}
                data={this.state.daftar}
                columns={[
                  { Header: "Nama Pembayaran", accessor: "nama_pembayaran" },
                  {
                    Header: "Nilai",
                    id: "nilai",
                    accessor: item => "Rp " + item.nilai
                  },
                  { Header: "Tgl", accessor: "tgl_pembayaran" },
                  { Header: "Petugas", accessor: "admin" },
                  {
                    Header: "Option",
                    id: "opsi",
                    accessor: item => (
                      <span>
                        <Button
                          color="info"
                          onClick={() =>
                            this.selectedPayment(
                              item.id_pembayaran,
                              item.kategori,
                              item.nilai,
                              item.idbyr
                            )
                          }
                        >
                          Edit
                        </Button>{" "}
                        <Button
                          color="danger"
                          onClick={() => this.deleteAlert(item.idbyr)}
                        >
                          Hapus
                        </Button>
                      </span>
                    )
                  }
                ]}
                defaultPageSize={5}
                className="-striped -highlight"
              />
            </Col>
          ) : null}
        </Row>
      </div>
    );
  }
}



